import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../dependencies/empresaMLFDependency';
import { CLASS_NAME, LancamentoFaturamento } from '../../../../models/dtos/mlf/lancamentoFaturamento';
import { SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES } from '../../../../models/enumerated/mlf/simplesNacionalSegregacaoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface LancamentoFaturamentoEditViewProps extends EditViewProps<LancamentoFaturamento> {}

const LancamentoFaturamentoEditView: FC<LancamentoFaturamentoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const formProps = useForm<any>({ defaultValues: selectedItem, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('empresa.id', globalReducer.globalParameter.empresa.id);
      }, 1000);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      title="Lançamento de Faturamento"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              placeholder="Informe a referência."
              type={InputType.REFERENCE}
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              placeholder="Informe o valor do faturamento"
              type={InputType.NUMBER}
            />
            <InputGroupInline
              id="simplesNacionalSegregacao"
              label="Segregação"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe a segregacao do simples nacional"
              inputDropDownProps={{ optionsProvider: SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default LancamentoFaturamentoEditView;
